html, body {
    width: 100vw;
    height: 100vh;
}

.calendar-screen {
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.rbc-calendar {
    height: 100%;
}

/* Modal */
.ReactModalPortal > div{
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 620px;
    max-width: 500px;
    outline: none;
    padding: 10px;
}

/* Datepicker */

.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border:  0 solid grey !important;
}

/* FABS */
.fab {
    border-radius: 100%;
    bottom: 25px;
    font-size: 30px;
    padding: 25px;
    position: fixed;
    right: 25px;
}

.fab-danger {
    bottom: 25px;
    padding: 10px;
    position: fixed;
    left: 25px;
}

.rbc-toolbar .rbc-toolbar-label {
    text-transform: capitalize;
}

.rbc-day-slot .rbc-events-container {
    margin-right: 0;
}

.rbc-agenda-view table.rbc-agenda-table tbody > tr > td {
    min-width: 130px;
}

#video {
    position: fixed;
    top: 0;
    z-index: -1;
}

.elv-1 {
    z-index: 4;
}

.btn-delete {
    top: 74px;
    left: 160px;
    width: 140px;
    height: 35px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 850px) {
    .btn-delete {
        top: 74px;
        width: fit-content;
    }

    .btn-delete span {
        display: none;
    }

    .rbc-btn-group {
        margin-bottom: 10px;
    }
    
    .rbc-toolbar-label {
        margin-left: 50px;
    }
}

@media (max-width: 640px) {
    .btn-delete {
        margin-right: 5px;
    }
    
}

@media (max-width: 600px) {
    .btn-delete {
        top: 66px;
        width: fit-content;
    }
}

